.App {
    text-align: center;
    display: flex;
    background-image: url('../img/3902.jpg'); /* Pfad zum Bild */
    background-size: cover; /* Das Bild wird den gesamten Hintergrund abdecken */
    background-repeat: no-repeat; /* Das Bild wird nicht wiederholt */
    background-attachment: fixed; /* Das Bild bleibt fixiert, wenn die Seite gescrollt wird */
}


.game {
  text-align: center;

  text-align: center;
  display: flex;
  width: 100%;
}


.spielfeld {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px 110px; /* Abstand zwischen den Bildern */
  justify-items: center; /* Zentrieren der Bilder horizontal */
  margin: 20px auto; /* Abstand um das Spielfeld */
  max-width: 1000px; /* Maximale Breite des Spielfelds */  
  height: 100vh;
  
}






.card {
  height: 199px;
  width: 150px;
  border: 10px solid grey;;
  border-radius: 40px;
}

.card img {
  max-width: 100%; /* Bildgröße auf 100% des Bildcontainers beschränken */
  height: auto; /* Bildhöhe automatisch anpassen */
}

.zugedeckt:hover{
  
  border: 10px solid #1fc210;
  border-radius: 40px;
  cursor: pointer;
}





@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}





.appstart{
  font-family: Arial, sans-serif;
  width: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

}

.startscreen {
  text-align: center;
  background-color: #f3c289;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 83%; /* Vertikale Position */
  transform: translate(-50%, -50%); /* Zentrierung bei 75% vertikaler Position */
  left: 50%;
}

.h1startscreen {
  font-size: 24px;
  margin-bottom: 20px;
}

.buttonstartscreen {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #ad4d0c;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 10px;
  width: 150px;
}

.music{
  width: 115px; /* Set a fixed width */
  text-align: center;
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden; /* Hide any overflow text */
  text-overflow: ellipsis; /* Show ellipsis (...) for overflow */
}

.buttonstartscreen:hover {
  background-color: #7e3403;
}


.anzeige{
  background-color: #f3c289;
  width: 15%;
}

.abstand{
  margin-top: 350px;
}





.player1{
  color:red;
  font-size: 25px;
}

.player2{
  color:blue;
  font-size: 25px;
}


.bold{
  margin-top: 140px;
  font-size: 30px;
}

.groesse{
  font-size: 24px;
}


.player1_solved_css{
  border: 10px solid red;
  border-radius: 40px;
  pointer-events: none;
}
.player2_solved_css{
  border: 10px solid blue;
  border-radius: 40px;
  pointer-events: none;
}



.modal{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(211, 135, 35, 0.5); /* #f3c289; */
  z-index: 1000; /* Passen Sie den z-Index an, um sicherzustellen, dass das Modal über anderen Inhalten liegt */
}

.modal-content{
  background-color: rgb(250, 212, 141);
  padding: 40px 60px;
  border-radius: 5px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  border-radius: 40px;
}